import axios from "axios"
import HeaderHome from "@/components/Home/HeaderHome/HeaderHome.vue"

export default {

  data() {
    return {
      baseUrl: "https://production-api-rentable.onrender.com/houses",
      photoUrl: null,
      housesList: [],
     
    }
  },



  mounted() {
    this.houses()
  },

  created() {
    this.houses()
  },

  methods: {
    async houses() {
      await axios.get(`${this.baseUrl}`).then(res => {
        this.housesList = res.data.reverse()
      })
    },

 

  },

    components: {
      HeaderHome,
    },
    
  }

